
import { defineComponent, SetupContext } from 'vue';
export default defineComponent({
  components: {
        
  },
  props: {
    value: {
      type: Boolean,
    }
  },
  setup(_, ctx: SetupContext) {
    const onChange = (e: InputEvent) => {
      const { target } = e;
      if (!(target instanceof HTMLInputElement)) return;
      const { value } = target;
      ctx.emit('input', value);
    };
    return {
      onChange
    }
  }
})
